<template>
  <transition name="slide">
      <div class="time-container">
        <c-header isBackNav title="时刻表"></c-header>
        <hs-section>
          <ul class="time-top">
            <li>{{$t('base.StationName')}}</li>
            <li>{{$t('base.Arrival')}}</li>
            <li>{{$t('base.Departure')}}</li>
            <li>{{$t('base.Stay')}}</li>
          </ul>
        </hs-section>
        <hs-scroll
        class="table-view"
        :data="timeTabeDatas"
        >
          <ul class="time-content" v-for="(item,index) in timeTabeDatas" :key="index">
            <li class="depate-container">
              <span :class="[item.Overdue!==-1?'left-red':'left-gray']">{{index+1}}</span>
              <span>{{item.StationName}}</span>
            </li>
            <li>{{item.DepartureTime}}</li>
            <li>{{item.ArrivalTime}}</li>
            <li>{{item.StopTime}}</li>
          </ul>
          <div :style="{height: '30px', width: '100%'}"></div>
        </hs-scroll>
      </div>
  </transition>
</template>

<script type='text/ecmascript-6'>
import CHeader from 'components/c-header/c-header';
import HsScroll from 'base/hs-scroll/hs-scroll';
import HsSection from 'base/hs-tableview/hs-section/hs-section';
import { kpiTrainGetTrainStopInfo } from 'api';
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection
  },
  props: {
    params: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      timeTabeDatas: []
    };
  },
  mounted() {
    console.log(this.params);
    kpiTrainGetTrainStopInfo(this.params).then(res => {
      console.log(res);
      if (res.IsSuccess) {
        this.timeTabeDatas = res.ResultData.StopInfos
      } else {
        this.$toast(this.$t('TM.FailedToGetPleaseTryAgainLater'))
      }
    })
  }
};
</script>

<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.time-container
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color $color_white
  z-index 100
  ul
    width 100%
    @extend .flex-row
    padding 15px 30px 15px 20px
    border-bottom 1px solid $color_gray_background_0
    li
      @extend .flex-row
      @extend .flex-end
      flex 1
      &:first-child
        flex 1.1
  .time-top
    height 50px
    border-bottom 3px solid $color_gray_background_0
    >li
      color $color-black_1
      font-weight bold
      &:first-child
        padding-right 5px
  .table-view
    width 100%
    height calc(100% - 95px)
    overflow hidden
    .time-content
      font-size 0.8rem
      .depate-container
        @extend .flex-row
        @extend .flex-start
        span
          &:first-child
            padding 2px 0px
            width 18px
            text-align center
            border-radius 9px
            font-size 0.6rem
            margin-right 10px
        .left-red
          background-color $color_theme
          color $color_white
        .left-gray
          background-color $color_gray_background_0
          color $color-black_1
.table-time-show-enter-active, .table-time-show-leave-active
  transition all 400ms ease-in-out
.table-time-show-enter
  opacity 0
.table-time-show-leave-active
  transform translateX(100%)
</style>



